import React, { useState } from 'react';

export default function App() {
	const questions = [
		{
			questionText: 'Hermione Granger es una estudiante de la siguiente casa:',
			answerOptions: [
				{ answerText: 'Slytherin', isCorrect: false },
				{ answerText: 'Hufflepuff', isCorrect: false },
				{ answerText: 'Gryffindor', isCorrect: true },
				{ answerText: 'Ravenclaw', isCorrect: false },
			],
		},
		{
			questionText: 'Boromir es el capitán de la ciudad:',
			answerOptions: [
				{ answerText: 'Osgiliath', isCorrect: false },
				{ answerText: 'Lorien', isCorrect: false },
				{ answerText: 'Gondor', isCorrect: true },
				{ answerText: 'Mordor', isCorrect: false },
			],
		},
		{
			questionText: ' ¿que recibió James Sunderland de su esposa?',
			answerOptions: [
				{ answerText: 'Carta', isCorrect: true },
				{ answerText: 'Foto', isCorrect: false },
				{ answerText: 'Diario', isCorrect: false },
				{ answerText: 'Grabación', isCorrect: false },
			],
		},
		{
			questionText: '¿que caballero dorado repara la armadura de Pegaso?',
			answerOptions: [
				{ answerText: 'Milo de escopión', isCorrect: false },
				{ answerText: 'Aioria de Leo', isCorrect: false },
				{ answerText: 'Shaka de Virgo', isCorrect: false },
				{ answerText: 'Mu de Aries', isCorrect: true },
			],
		},
	];

	const [currentQuestion, setCurrentQuestion] = useState(0);
	const [showScore, setShowScore] = useState(false);
	const [score, setScore] = useState(0);
	

	const handleAnswerOptionClick = (isCorrect) => {
		if (isCorrect) {
			setScore(score + 1);
		}

		const nextQuestion = currentQuestion + 1;
		if (nextQuestion < questions.length) {
			setCurrentQuestion(nextQuestion);
		} else {
			setShowScore(true);
		}
	};
	return (
		<div className='app'>
			{showScore ? (
				<div className='score-section'>
					tu puntaje {score} de {questions.length}
				</div>
			) : (
				<>
					<div className='question-section'>
						<div className='question-count'>
							<span>Preguntas {currentQuestion + 1}</span>/{questions.length}
						</div>
						<div className='question-text'>{questions[currentQuestion].questionText}</div>
					</div>
					<div className='answer-section'>
						{questions[currentQuestion].answerOptions.map((answerOption) => (
							<button onClick={() => handleAnswerOptionClick(answerOption.isCorrect)}>{answerOption.answerText}</button>
						))}
					</div>
                   
				</>
			)}
		</div>

	);
}
